.footer {
    padding-top: 2rem;
    margin-top: 7rem;
    position: relative;

    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        background-image: url(../img/icons/wave-border.svg);
        width: calc(100% + 2vw + 9px);
        height: 29px;
        transform: translateY(-100%);
    }
    &__inner {
        display: flex;
        width: 100%;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    &__contacts {
        display: flex;
        align-items: center;
    }
    &__contacts-prefix {
        display: flex;
        flex-direction: column;
        font-size: 14px;
        justify-content: center;
        margin-right: 0.5em;
        line-height: 1em;
    }
    &__contacts-tel {
        position: relative;
        font-size: 38px;
        font-weight: 500;
        align-items: center;
        line-height: 1em;
    }
    &__contacts-sub {
        position: absolute;
        bottom: 0;
        transform: translateY(100%);
        white-space: nowrap;
        font-size: 14px;
        line-height: 1em;
        color: rgba($color-main, 0.5);
        right: 0;
        padding-left: 15px;
        background-color: transparent;
        border: none;

        &:before {
            content: '';
            width: 10px;
            height: 1em;
            position: absolute;
            background-image: url(../img/icons/phone.png);
            background-size: contain;
            background-repeat: no-repeat;
            left: 0;
            background-position: center;
        }
    }
    &__timing {
        font-size: 14px;
        line-height: 1.5em;
        margin: 0 1.5em;
    }
    &__bamboo {
        &--left {
            display: flex;

            .bamboo__cell {
                padding: 0;
            }
        }
        &--right {
            display: flex;
            margin-left: -5px;
        }
    }
    &__timing-text {
        color: rgba($color-main, 0.5);
    }
    &__time {
        font-size: 22px;
        font-weight: 500;
    }
    &__link {
        font-family: $font-secondary;
        font-size: 16px;
        font-weight: 500;
        line-height: 1em;
        padding-top: 2px;
        text-transform: uppercase;
        white-space: nowrap;
    }
}

// <=1366px
@include media-breakpoint-down(md) {
    .footer {
        &__inner {
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
        }
        &__bamboo {
            margin-bottom: 1rem;
        }
    }
}

// <=768px
@include media-breakpoint-down(pl) {
    .footer {
        &__bamboo {
            flex-direction: column;
            align-items: center;
            margin: 0 0 1rem;
            width: 100%;

            &:before, &:after {
                content: none;
            }

            &--left {
                margin-bottom: 2rem;
                width: 100%;

                .bamboo__cell {
                    width: 25%;
                }
            }
            &--right {
                margin: 0;
                width: 100%;


            }
            .bamboo__cell {
                width: 50%;
                padding: 0;
                min-width: auto;
            }
        }
        &__timing {
            margin: 1rem 1.5rem;
        }
    }
}