body {
  width: 100%;
  height: auto;
  font-size: 1.0625rem;
  font-family: "DINRoundPro", sans-serif;
  font-weight: 400;
  line-height: 1.64706;
  color: #472821;
  min-width: 320px;
  overflow-x: hidden;
  -moz-osx-font-smoothing: grayscale;
  /* Firefox */
  -webkit-font-smoothing: antialiased;
  /* WebKit  */
  background-color: #F14F10;
  padding: 0 1vw;
}

body::selection {
  background-color: salmon;
  color: #FFF;
}

.layout {
  background-color: #FEF5E3;
  display: block;
  width: 100%;
  height: 100%;
  padding: 30px 2% 70px;
  position: relative;
  min-height: 100vh;
}

button, input, textarea {
  outline: none;
}

button:focus, input:focus, textarea:focus {
  outline: none;
}

button:active, input:active, textarea:active {
  outline: none;
}

button {
  user-select: none;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  font-family: 'DinCondensed';
  line-height: 1em;
}

p {
  margin: 0;
}

a {
  color: inherit;
}

a:hover {
  color: inherit;
  text-decoration: none;
}

@media (max-width: 1023.98px) {
  .layout {
    padding-top: 100px;
  }
  html {
    overflow-x: hidden;
  }
}

@media (max-width: 767.98px) {
  body {
    padding: 0 2vw;
  }
}

/**
 * Unidirectional margins
 * Remove all top margins and normalise bottom margins
*/
h1,
h2,
h3,
h4,
h5,
h6,
blockquote,
figure,
ul,
ol,
dl,
address {
  margin-top: 0;
  margin-bottom: 1.5rem;
}

.icon {
  display: inline-block;
  line-height: 1;
}

.cart__list::-webkit-scrollbar {
  width: 5px;
}

.cart__list::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px #e9efdb;
  background-color: #e9efdb;
  border-radius: 10px;
}

.cart__list::-webkit-scrollbar-thumb {
  background-color: #2EBD9D;
  outline: 1px solid slategrey;
  border-radius: 10px;
}

.hot .products__name, .hot .product-set__product-name, .hot .product-card__name {
  padding-left: 30px;
  background-image: url(../img/icons/pepper.svg);
  background-repeat: no-repeat;
  background-position: left 0.4em;
  background-size: 1.4em;
}

.hot .product-card__name {
  padding-left: 50px;
  background-size: 10%;
}

.discounted .discount {
  display: flex;
}

.bamboo {
  margin: 0 1rem;
  position: relative;
  display: flex;
  justify-content: center;
}

.bamboo__button {
  background-color: transparent;
  border: none;
  height: 100%;
  font-family: "DinCondensed", sans-serif;
  font-weight: 500;
  font-size: 17px;
  text-transform: uppercase;
  line-height: 50px;
}

.bamboo__cell {
  padding: 0 2rem;
  position: relative;
  height: 50px;
  display: flex;
  align-items: center;
  min-width: 5rem;
  justify-content: center;
  border-top: 1px solid #2EBD9D;
  border-bottom: 1px solid #2EBD9D;
  animation: 1s linear infinite;
  background-image: url(../img/icons/wave.svg);
}

.bamboo__cell--black {
  border-color: #472821;
  background-image: url(../img/icons/wave-red.svg);
}

.bamboo__cell--black:after, .bamboo__cell--black:before {
  background-color: #472821 !important;
}

.bamboo__cell--black:hover {
  background-image: url(../img/icons/wave-red-op.svg) !important;
}

.bamboo__cell:hover {
  animation-name: waving;
  background-image: url(../img/icons/wave-op.svg);
}

.bamboo__cell:after, .bamboo__cell:first-child:before {
  content: '';
  background-color: #2EBD9D;
  position: absolute;
  width: 5px;
  height: 130%;
  border-radius: 5px;
  top: -15%;
  right: 0;
}

.bamboo__cell:first-child:before {
  left: 0;
}

.bamboo:before, .bamboo:after {
  content: '';
  background-image: url(../img/icons/bamboo.svg);
  height: 34px;
  width: 16px;
  position: absolute;
  background-size: cover;
  background-repeat: no-repeat;
  top: 50%;
  transform: translateY(-50%);
}

.bamboo:before {
  left: -20px;
}

.bamboo:after {
  right: -20px;
}

.bamboo--no-signs {
  margin: 0;
}

.bamboo--no-signs:after, .bamboo--no-signs:before {
  content: none;
}

.bamboo--top-menu .bamboo__cell {
  background-color: #FEF5E3;
}

@keyframes waving {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 0 62px;
  }
}

@media (max-width: 1023.98px) {
  .bamboo--no-sighns {
    margin: 0;
  }
  .bamboo__cell.search {
    display: none;
  }
  .bamboo__cell--mob-header {
    background-image: none;
    border: none;
    width: calc(100% - 2vw);
    left: 1vw;
    height: 80px;
    position: fixed;
    padding: 0 2vw;
    top: 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #F8E4CD;
    background-color: #FEF5E3;
  }
  .bamboo__cell--mob-header:after {
    content: none;
  }
  .bamboo--top-menu:before, .bamboo--top-menu:after {
    content: none;
  }
}

@media (max-width: 767.98px) {
  .bamboo__cell--mob-header {
    width: calc(100% - 4vw);
    left: 2vw;
  }
}

#sprite {
  display: none;
}

.sprite__default {
  width: 20px;
  height: 20px;
}

.sprite__search {
  width: 20px;
  height: 20px;
}

.search {
  padding: 0;
}

.search__input {
  border: none;
  background-color: transparent;
  height: 100%;
  max-width: 0;
  text-transform: uppercase;
  font-weight: 500;
  transition: max-width 0.8s ease-out, padding 0.4s ease-in;
  font-family: "DinCondensed", sans-serif;
  padding-left: 0;
}

.search__button {
  display: flex;
  background-color: transparent;
  border: none;
  animation: 1s linear transform;
  height: 100%;
  padding: 0;
  width: 80px;
  justify-content: center;
}

.search__button:hover {
  animation-name: shake-it-baby;
}

@keyframes shake-it-baby {
  25% {
    transform: rotate(16deg);
  }
  50% {
    transform: rotate(0);
  }
  75% {
    transform: rotate(-16deg);
  }
  100% {
    transform: rotate(0);
  }
}

.search.active .search__input {
  max-width: 600px;
  padding: 1em;
}

.ticker {
  position: absolute;
  height: 100%;
  width: 50px;
  right: calc(-2vw + -9px);
  top: 3.5rem;
  overflow: hidden;
  pointer-events: none;
}

.ticker:before, .ticker:after {
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 30px;
  z-index: 1;
}

.ticker:before {
  top: 0;
  background: linear-gradient(#FEF5E3, transparent);
}

.ticker:after {
  bottom: 0;
  background: linear-gradient(transparent, #FEF5E3);
}

.ticker__wrapper {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  transform: rotate(-90deg) translate(30px, 34px);
  transform-origin: bottom;
}

.ticker__text {
  animation: ticker 3s linear infinite;
  transform-origin: left;
  text-transform: uppercase;
  font-family: "DinCondensed", sans-serif;
  font-size: 50px;
  line-height: 1em;
  white-space: nowrap;
  padding-right: 0.2em;
  transform: translateX(-100%);
  color: #FEF5E3;
  text-shadow: #F14F10 1px 1px 1px, #F14F10 -1px -1px 1px, #F14F10 -1px 1px 1px, #F14F10 1px -1px 1px;
}

@keyframes ticker {
  100% {
    transform: translateX(0);
  }
}

@media (max-width: 767.98px) {
  .ticker {
    display: none;
  }
}

.subscribe {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80px;
}

.subscribe__icon {
  width: 18px;
  height: 18px;
}

.subscribe .subscribe-ink {
