.keywords {
  margin: 20px 0;
}

.keywords__box {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.keywords__item {
  height: 60px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  background-color: #2EBD9D;
  color: white;
  padding: 0 2rem;
  margin-bottom: 10px;
  margin-right: 10px;
  border: none;
  transition: 0.5s ease;
}

.keywords__item:hover {
  background-color: #32af93;
  color: white !important;
}

.keywords__item:last-child {
  margin-right: 0;
}

.keywords__cross {
  background: transparent;
  border: none;
  position: relative;
  width: 14px;
  height: 14px;
  padding: 0;
  margin-right: 15px;
}

.keywords__cross:before, .keywords__cross:after {
  content: "";
  position: absolute;
  height: 100%;
  width: 1px;
  background-color: white;
  top: 50%;
  left: 50%;
  pointer-events: none;
}

.keywords__cross:before {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.keywords__cross:after {
  transform: translate(-50%, -50%) rotate(45deg);
}

.keywords__text {
  font-size: 14px;
  font-weight: 500;
  line-height: 1em;
  text-transform: uppercase;
}

.keywords--catalog .keywords__box {
  justify-content: flex-start;
}

.keywords--catalog .keywords__item {
  height: 26px;
  background-color: #F14F10;
  border-radius: 26px;
  padding: 0 1rem;
}

.keywords--catalog .keywords__item:hover {
  background-color: #dc4f18;
  color: white !important;
}

.keywords--catalog .keywords__text {
  font-size: 14px;
  text-transform: none;
  font-weight: 400;
  padding-bottom: 2px;
}

.keywords--catalog .keywords__cross {
  height: 12px;
  width: 12px;
  margin-right: 10px;
  opacity: 0.5;
  transition: 0.3s ease;
}

@media (max-width: 767.98px) {
  .keywords__item {
    padding: 0 1.35rem;
    height: 40px;
  }
}

