.rellax {
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    position: absolute;
    max-height: 100vh;
    pointer-events: none;

    &--top {
        z-index: 2;
    }

    &--set {
        height: 65%;
    }
    &__layout {
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        position: relative;
    }


    &__item {
        width: 50px;
        height: 60px;
        position: absolute;
    }

    &__roll-p {
        width: 35px;
        right: 5%;
        top: 35%;

        &--rolls {
            width: 39px;
            top: 70%;
            right: 10%;
        }
    }
    &__roll-sm-p {
        width: 25px;
        top: 8%;
        left: 3%;
    }
    &__grib-p {
        &--pizza {
            width: 23px;
            top: -30%;
            right: 0;
        }
    }
    &__fish-p {
        width: 79px;
        top: 50%;
        left: 2%;

        &--rolls {
            width: 79px;
            bottom: 0;
            left: 0;
        }
    }
    &__fishy-p {
        top: 8%;
        right: 10%;
        width: 30px;
    }
    &__samuray-p {
        width: 36px;
        top: 2%;
        left: 20%;
    }
    &__pizza-full-p {
        &--pizza {
            width: 56px;
            top: 20%;
            left: 10%;
        }
    }
    &__sleepy-p {
        width: 58px;
        top: 25%;
        left: 72%;

        &--pizza {

        }
        &--rolls {
            width: 79px;
            top: 0%;
            left: 5%;
        }
    }
    &__pizza-p {
        top: 50%;
        right: 14%;
        width: 63px;

        &--pizza {
            top: -70%;
            left: 10%;
            width: 63px;
        }
    }
    &__palki-p {
        &--noodles {
            top: -80%;
            right: 20%;
            width: 63px;
        }
    }
    &__lapsha-p {
        top: 27%;
        left: 14%;
        width: 37px;
    }
    &__sym-p-1 {
        width: 41px;
        height: 43px;
        top: -5px;
        left: 45%;
    }
    &__sym-p-2 {
        height: 46px;
        width: 48px;
        right: -10px;
        top: 35vh;
    }
    &__sym-p-3 {
        left: -10px;
        top: 50vh;
        height: 56px;
        width: 58px;
    }
    &__sym-p-4 {
        height: 46px;
        width: 48px;
        right: -10px;
        top: 85vh;
    }
}

// <=1024px
@include media-breakpoint-down(tablet) {
    .rellax--top {
        display: none;
    }
}