.pop.active {
    display: block;
}
.pop {
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 6;
    display: none;

    &--call {
        .bamboo {
            margin-top: 1rem;
        }
    }

    &__bg {
        background-color: rgba(241,79,16,0.8);
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
    }
    &__inner {
        background-color: #FEF5E3;
        border-radius: 11px;
        position: relative;
        padding: 5vw;
        width: 50%;
        margin: auto;
        top: 50%;
        transform: translateY(-50%);
        min-width: 300px;
        max-height: 90%;
        overflow-x: hidden;
    }
    &__cross {
        position: absolute;
        top: 30px;
        right: 30px;
    }
    &__cross-in {
        position: relative;
        width: 20px;
        height: 20px;
        border: none;
        background: transparent;

        &:after, &:before {
            content: '';
            position: absolute;
            height: 100%;
            width: 2px;
            background-color: $brown;
            top: 50%;
            left: 50%;
            pointer-events: none;
        }
        &:before {
            transform: translate(-50%, -50%) rotate(45deg);
        }
        &:after {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
    }
    &__sym {
        opacity: .4;
        background-image: url(../img/icons/big-sym.svg);
        background-position: center 4%;
        background-size: 60%;
        background-repeat: no-repeat;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        pointer-events: none;
    }
    &__title {
        font-family: $font-secondary;
        font-size: 46px;
        font-weight: 500;
        line-height: 46px;
        text-align: center;
    }
    &__text {
        font-size: 14px;
        line-height: 18px;
        text-align: center;
        margin-top: 1em;
        margin-bottom: 2em;
        color: rgba($brown, 0.5)
    }
    &--success {
        .pop {
            &__inner {
                max-width: 460px;
            }
        }
    }
}

// <=768px
@include media-breakpoint-down(pl) {
    .pop {
        &__inner {
            overflow-y: scroll;
        }
    }
}