.subscribe {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80px;

    &__icon {
        width: 18px;
        height: 18px;
    }

    .subscribe-ink {
        fill: $brown;
    }

    &:hover {
        .subscribe-ink {
            fill: $green;
        }
    }
}