.of-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: 'object-fit: cover;';
}

.of-contain {
  width: 100%;
  height: 100%;
  object-fit: contain;
  font-family: 'object-fit: contain;';
}

.of-contain-top {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: 50% 0%;
  font-family: 'object-fit: contain; object-position: 50% 0;';
}